.business-wrapper {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0; }

#price-of-service {
  padding-top: 50px;
  padding-bottom: 50px;
  margin: auto; }

#business-hero {
  height: 490px !important;
  display: flex;
  padding-left: 120px;
  padding-top: 50px;
  margin: auto;
  flex-direction: row;
  @media screen and (max-width: $small-laptop-screen) {
    height: 450px !important;
    padding-left: 70px; }
  @media screen and (max-width: $tablet) {
    flex-direction: column;
    height: 385px !important;
    width: 80%;
    padding-top: 60px;
    padding-left: 0px;
    #signup {
      display: none; } }
  @media screen and (max-width: $mobile) {
    height: 400px !important;
    width: 85%;
    padding-top: 28px; }

  p {
    margin-bottom: 25px; }

  h2 {
  	font-family: GothamRounded-Medium;
  	font-size: 45px;
  	line-height: 55px;
  	margin-bottom: 0px;
  	margin-top: 0px; }

  article {
    margin: 0px auto; }

  a {
    color: #fff;
    border: 2px solid;
    padding: 9px; }

  .signup-form {
    width: 60% !important;
    height: 450px;
    @media screen and (max-width: $small-laptop-screen) {
      width: 80% !important;
      height: 430px; } } }

.mobile-signup {
  @media screen and (min-width: $small-laptop-screen) {
    display: none !important; }
  @media screen and (max-width: $tablet) {
    height: 520px !important;
    .signup-form {
      width: 90% !important;
      height: 465px !important;
      h1 {
        padding-left: 0px !important;
        text-align: center; } } } }



#how-it-works {
  display: flex;
  width: 80%;
  margin: auto;
  height: 200px;
  @media screen and (max-width: $tablet) {
    width: 90%;
    flex-direction: column;
    height: 450px; }
  @media screen and (max-width: $mobile) {
    height: 600px; } }

.how-it-works--step {
  display: flex;
  width: 100%;

  @media screen and (max-width: $tablet) {
    padding-bottom: 50px; } }


.how-it-works--step-number {
  font-size: 4em;
  margin-top: 20px !important;
  margin-right: 10px !important; }

.how-it-works--step-highlight {
  font-family: GothamRounded-Bold;
  font-size: 18px; }

.section-title {
  text-align: center;
  font-family: GothamRounded-Bold;
  font-size: 30px;
  line-height: 45px;
  margin-bottom: 80px !important;
  margin-top: 90px !important;
  width: 80%;
  margin: auto;
  @media screen and (max-width: $tablet) {
    margin-bottom: 20px; }
  @media screen and (max-width: $mobile) {
    margin-bottom: 50px; } }
